import React from 'react';
import PropTypes from 'prop-types';
import {Button, Div, FormLayout, Group, Input, Panel, PanelHeader} from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
bridge
  .send('VKWebAppGetUserInfo')
  .then(data => {
    console.log(data.id);
  })

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>VKCASH</PanelHeader>
		<Group>
			<Div align="center">
			Зарабатывай, просто приглашая друзей
			<br/>
            Тот кого Вы пригласили должен ввести в поле ниже Ваш код: 
			<br/>
            Вы оба получите +1р
			<FormLayout>
					<Input type="text" placeholder="Реферальный код"/>
				</FormLayout>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					Готово
				</Button>
            </Div>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
